.banner{
    color:white;
    object-fit: contain;
    height: 300px;
    padding-top: 180px;
    padding-left: 20px;
}
.banner_contents{
    margin-left: 40px;
    padding-top: 340px;
    height: 300px;
}
.banner_title{
    font-size:3rem;
    font-weight:800;
    padding-bottom: 0.3rem;
}
.banner_description{
    width:50 rem;
    line-height: 1.3;
    padding-top:1rem;
    font-size: 0.9rem;
    max-width: 500px;
    height: 80px;
    color: rgb(187, 191, 191);
}
.banner_button{
    cursor: pointer;
    color:#fff;
    outline:none;
    border:none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    padding-bottom: 0.5rem;
}
.banner_button:hover{
    color:#000;
    background-color: #e6e6e6;
    transition: all 0.2s;
}
.banner_fadebottom{
    height: 7.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37,0.61),
        #111
    );
}